import { Metadata } from 'next';
import Breadcrumbs from '@/app/ui/menu/breadcrumbs';
import TransaksiForm from '@/app/ui/transaksi/createform';
import { fetchProdukList } from '@/app/lib/actions';

export const metadata: Metadata = {
  title: 'Buat Transaksi',
};

export default async function CreateTransaksiPage() {
  let produkList;
  
  try {
    produkList = await fetchProdukList();
  } catch (error) {
    console.error('Failed to fetch produk list:', error);
    // Fallback jika gagal fetch produk
    produkList = [];
  }

  // Jika tidak ada produk, redirect atau tampilkan pesan
  if (produkList.length === 0) {
    return (
      <main>
        <Breadcrumbs
          breadcrumbs={[
            { label: 'Transaksi', href: '/admin/dashboard/transaksi' },
            {
              label: 'Buat Transaksi',
              href: '/admin/dashboard/transaksi/create',
              active: true,
            },
          ]}
        />
        <div className="mt-8 p-6 bg-yellow-50 border border-yellow-200 rounded-lg">
          <h2 className="text-lg font-semibold text-yellow-800 mb-2">
            Tidak Ada Produk
          </h2>
          <p className="text-yellow-700">
            Anda perlu menambahkan produk terlebih dahulu sebelum membuat transaksi.
          </p>
          <a 
            href="/admin/dashboard/produk/create"
            className="mt-3 inline-block px-4 py-2 bg-yellow-600 text-white rounded hover:bg-yellow-700"
          >
            Tambah Produk
          </a>
        </div>
      </main>
    );
  }

  return (
    <main>
      <Breadcrumbs
        breadcrumbs={[
          { label: 'Transaksi', href: '/admin/dashboard/transaksi' },
          {
            label: 'Buat Transaksi',
            href: '/admin/dashboard/transaksi/create',
            active: true,
          },
        ]}
      />
      <div className="mt-8">
        <h1 className="text-2xl font-bold mb-6">Buat Transaksi Baru</h1>
        <TransaksiForm produkList={produkList} />
      </div>
    </main>
  );
}